.App {
    font-family: sans-serif;
    text-align: center;
}

.grid {
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    min-height: 52vh;
    min-width: 350px;
    max-width: 400px;
    gap: 5px;
    padding: 4px;
    background-color: rgb(247, 245, 245);
}

.row {
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    gap: 5px;
}

.tile {
    background-color: royalblue;
    border: 4px solid white;
    flex-grow: 1;
    font-size: 30px;
    width: 80px;
    height: 55px;
    border-radius: 10%;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.matched {
    background-color: green;
}

.present {
    background-color: grey;
}

.notpresent {
    background-color: darkred;
}